import { Component, OnInit } from '@angular/core';
import { MessageService, ConfirmationService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { User, UserGrouped } from '../../../models/user.model';
import { RoleType } from '../../../models/role.type.enum'
import { UserService } from "../../../services/user.service";
import { AddComponent } from '../add/add.component';
import { AddressInfoComponent } from 'src/app/modules/order-management/address-info/address-info.component';
import { FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class ListComponent implements OnInit {
  basicUsers: User[] = [];
  constructor(
    private dialogService: DialogService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private UserService: UserService,
    private fb: FormBuilder
  ) { }

  ngOnInit(): void {
    this.getAllBasicUsers()
  }

  getAllBasicUsers() {
    this.UserService.getAllUsers(RoleType.basic).subscribe((res: UserGrouped) => {
      this.basicUsers = res.data;
    })
  }

  openNew(mode: string, data?) {
    const ref = this.UserService.addOrEdit(mode, data, "basic", this.dialogService, this.fb)
    ref.onClose.subscribe(data => {
      if (data) {
        this.messageService.add({ severity: 'Success', summary: 'User Action', detail: data, life: 3000 });
        this.getAllBasicUsers();
      }
    })
  }

  deleteUser(user: User) {
    console.log(user)
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete ' + user.userName + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.basicUsers = this.basicUsers.filter(u => u.id !== user.id);
        this.UserService.deleteUser(user.id).subscribe(data => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: 'User Deleted', life: 3000 });
        })
      }
    });
  }

  getAddress(addressId) {
    const ref = this.UserService.getAddressInfo(addressId, this.dialogService)
  }

}
